.widgetLg {
    flex: 2; 
    -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    padding: 20px;
  }
  .widgetLgTitle{
    font-size: 22px;
    font-weight: 600;
  }
  .widgetLgImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
  }
  .widgetLgButton {
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
  }